.main {
  padding: 20px;
  text-align: center;
}

.main img {
  margin: 0 0 15px 0;
}

.main .joke {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 28px;
  color: #ababab;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  margin-bottom: 15px;
}

.main .id {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 14px;
  color: #ababab;
  font-weight: 400;
  text-align: center;
  margin: 0 40px;
  padding-top: 10px;
  border-top: 1px solid rgba(42, 174, 232, 0.25);
}

.main .btn {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 20px;
  color: white;
  background: #2aaee8;
  font-weight: 400;
  text-align: center;
  padding: 10px 30px;
  border: none;
  border-radius: 500px;
  margin-top: 30px;
  cursor: pointer;
}



