.footer {
  font-family: "Source Sans Pro", sans-serif;
  color: #2aaee8;
  font-size: 14px;
  text-align: center;
  margin-bottom: 20px;
  margin-top: 30px;
}

.footer .red {
  color: red;
}

.footer a,
.footer a:hover,
.footer a:active,
.footer a:visited,
.footer a:focus {
  text-decoration: underline;
  color: #2aaee8;
  -webkit-text-decoration-style: dotted;
          text-decoration-style: dotted;
}



