.header {
  font-family: "Source Sans Pro", sans-serif;
  background: #2aaee8;
  overflow: hidden;
  color: white;
  padding: 10px 20px;
  margin-bottom: 10px;
}

.header .menu {
  font-size: 20px;
  width: 10%;
  float: left;
  text-align: left;
}

.header .title {
  font-size: 22px;
  width: 80%;
  float: left;
  text-align: center;
}

.header .escape {
  visibility: hidden;
  font-size: 20px;
  width: 10%;
  float: left;
  text-align: right;
}



