.loader {
  text-align: center;
}

.loader .avatar {
  -webkit-animation: avatarAnim;
          animation: avatarAnim;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes avatarAnim {
  0% {
    -webkit-transform: scale(0.5) rotateX(0deg);
            transform: scale(0.5) rotateX(0deg);
  }
  50% {
    -webkit-transform: scale(1.25) rotateX(360deg);
            transform: scale(1.25) rotateX(360deg);
  }
  100% {
    -webkit-transform: scale(0.5) rotateX(720deg);
            transform: scale(0.5) rotateX(720deg);
  }
}

@keyframes avatarAnim {
  0% {
    -webkit-transform: scale(0.5) rotateX(0deg);
            transform: scale(0.5) rotateX(0deg);
  }
  50% {
    -webkit-transform: scale(1.25) rotateX(360deg);
            transform: scale(1.25) rotateX(360deg);
  }
  100% {
    -webkit-transform: scale(0.5) rotateX(720deg);
            transform: scale(0.5) rotateX(720deg);
  }
}



