.faq {
  font-family: "Source Sans Pro", sans-serif;
  text-align: left;
  padding: 0 30px;
}

.faq h3 {
  color: #ababab;
  font-size: 30px;
  font-weight: 600;
  margin-bottom: 25px;
}

.faq p {
  margin: 0;
}

.faq .question {
  text-align: justify;
  color: #2aaee8;
  margin-top: 20px;
  font-weight: 600;
}

.faq .answer {
  text-align: justify;
  color: #ababab;
  font-weight: 400;
}

.faq .space {
  height: 20px;
  visibility: hidden;
}



